@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600;700;800&display=swap);
:root {
  --primary-color: #D7FD35;
  --dark-bg: #323232;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  font-family: 'Montserrat', sans-serif;
}

.text-bold {
  font-weight: 800;
}

.all-titles {
  color: var(--primary-color);
  font-weight: 800;
  font-size: 60px;
}

.all-text {
  color: white;
  font-size: 18px;
}
.btn-style {
  background-color: var(--primary-color) !important;
  border: black;
  color: black;
  margin-right: 40px;
  font-weight: bold !important;
  width: 306px;
  height: 53px;
  font-size: 24px !important;
}

.btn-style:hover {
  background-color: rgba(215, 253, 53, 0.7) !important;
}

.home-page {
  background-image: url('/bg-bw.jpeg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 93vh;
}

.home-info {
  width: 50%;
  margin-left: 76px;
}

.home-title {
  color: white;
  font-size: 96px;
  text-align: left;
}

.home-text {
  text-align: left;
  font-size: 22px;
  color: var(--primary-color);
  margin-top: 20px;
}

.home-buttons {
  margin: 80px auto 0;
}
.contact-page {
  background-color: var(--dark-bg);
  height: 92vh;
}

.contact-info {
  margin-left: 70px;
  padding-top: 30px;
}

.login-page {
  background-color: var(--dark-bg);
  height: 91vh;
}

.login-bg {
  background-color: var(--primary-color);
  height: 91vh;
  width: 50%;
  margin: 0 auto;
}

.login-title {
  font-size: 76px;
  font-weight: 700;
}

.login-text {
  font-size: 18px;
  width: 50%;
  margin: 0 auto;
}

.form-fields {
  font-size: 20px;
}

.input-width {
  width: 338px;
}

.login-button {
  width: 338px;
  height: 43px;
  background-color: var(--dark-bg);
  color: white;
  font-weight: 700;
}


.form-text {
  margin: 0;
}

.master-form {
  background-color: var(--dark-bg);
  width: 100vw;
}

.btn-pagination {
  background-color: var(--primary-color) !important;
  border-radius: 25px !important;
  font-weight: 600 !important;
  margin-right: 20px;
  margin-top: 20px;
}

.form-steps {
  background-color: white;
  padding: 40px 0;
}

.all-sections {
  width: 100%;
}

.section-1 {

}

.green-bar {
  background-color: var(--primary-color);
  height: 2px;
}

.form-field {
  font-size: 16px;
}
nav {
  height: 60px;
}

nav ul {
  width: 100%;
  margin: 0 60px;
}

.bg-navbar {
  background-color: var(--dark-bg) !important;
}

.navbar-items {
  display: flex;
  justify-content: end;
}
